@import "vars";
@import "preloder";

* {
    box-sizing: border-box;
}

input, select, textarea {
    font-family: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    color: #333333;
    font-size: 18px;
}

h1, h2, h3 {
    font-weight: lighter;
    color: $dark__color;
}

h1 {
    font-size: 3rem;
}

.t-center {
    text-align: center;
}
.subtitle {
    line-height: 1.6;
}

.container {
    max-width: 1380px;
    padding: 0 1rem;
    margin: 0 auto;
}

.header {
    min-height: 40px;
    background-color: $dark__color;
}

.main {
    padding-bottom: 3rem;
}

.head {
    h2 {
        font-weight: lighter;
    }
    border-bottom: 1px solid #eee;
    margin-bottom: 40px;
}

.check-container {
    max-width: 980px;
    margin: 0 auto;
    @include flex(column, center, center);
    
    .title, .subtitle {
        max-width: 740px;
    }
}

.check-form {
    width: 100%;
    max-width: 440px;
    text-align: center;
}

.check-input {
    @include flex(row, center);
    
    input {
        box-sizing: border-box;
        width: 100%;
        height: 40px;
        box-shadow: inset 0 2px 2px #C2C2C2;
        color: #262626;
        -webkit-font-smoothing: antialiased;
        border: 1px solid #D6D6D6;
    }
    
    button {
        border: none;
        border-radius: 0;
        cursor: pointer;
        font-size: 1.1rem;
    }
}
